.search {
    padding-left: 15px;
    display: block;
    margin: 0 auto;
    margin-top: 40px;

    width: 560px;
    height: 70px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
}

.text{
    cursor: default;
    width: 560px;
    margin: 0 auto;
    margin-top: 40px;
    padding: 17px;
    background: #447FC1;
    box-sizing: border-box;
    border-radius: 15px;
    
    color: white;
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
}

.back__button{
    cursor: pointer;
    position: absolute;
    top: 200px;
    left: 400px;
    width: 20px;
}