.group__number{
    margin: 0 auto;
    margin-top: 35px;
    width: 600px;
    height: 85px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 32px;
    padding: 20px;
    padding-left: 65px;
}

.chooseWeek{

    width: 670px;
    margin: 0 auto;
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nextWeek__img{
    width: 15px;
}
.prevWeek__img{
    width: 15px;
}

.weekBounds{
    padding-top: 13px;
    width: 182px;
    height: 65px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    text-align: center;

    color: #000000;
}

.nextWeek__text{
    display: block;
    width: 164px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    text-align: center;
    margin-left: 9px;
    color: #000000;
}

.nextWeek__text::after{
    content: '';
    background-image: url(./right.svg);
    width: 18px;
    height: 30px;
    position: absolute;
    background-size: cover;
    top: 297px;
    right: 400px;
}

.nextWeek{
    cursor: pointer;
    padding-top: 5px;
    width: 224px;
    height: 65px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;
}

.prevWeek__text{
    display: block;
    width: 164px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    text-align: center;
    margin-right: 14px;
    margin-left: auto;
    color: #000000;
}

.prevWeek__text::before{
    content: '';
    background-image: url(./left.svg);
    width: 18px;
    height: 30px;
    position: absolute;
    background-size: cover;
    top: 297px;
    left: 400px;
}

.prevWeek{
    cursor: pointer;
    padding-top: 5px;
    width: 224px;
    height: 65px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;
}

.back__btn{
    cursor: pointer;
    margin: 0 auto;
    margin-top: 18px;
    padding-top: 9px;
    width: 182px;
    height: 50px;

    background: #FFFFFF;
    border: 4px solid #FFA573;
    box-sizing: border-box;
    border-radius: 15px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    text-align: center;

    color: #000000;
}

/* расписание по дням */

.days__wrapper{
    margin: 0 auto;
}

.day{
    margin-top: 33px;
    display: flex;
    justify-content: space-between;

    list-style-type: none;
    background: #FFFFFF;
    border: 4px solid #447FC1;
    box-sizing: border-box;
    border-radius: 15px;
}

.allDaySubjects{
    border-left: 3px dashed #C2A4E6;
}

.weekDay{
    width: 215px;
    text-align: center;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 36px;
    align-self: center;

    color: #000000;
}

.oneSubj{
    padding: 30px 0;
    padding-left: 15px;
    width: 727px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 3px dashed #FFA573;;
}

.oneSubj:last-child{
    border-bottom: none;
}

.subj{
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 28px;
    display: flex;
    align-items: center;
